*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

html{
  font-size: 62.5%;

}

body{
  background-color: rgb(219,230,253);
}

h2{
  font-size: 3.2rem;
  margin: 2rem 0;
  text-align: center;

}

p{
  margin: 2rem 0;
  color: black;
  font-weight: 400;
  font-size: 1.6rem;
  display: inline-block;


}

span{
  text-transform: capitalize;
  font-weight: bold;
}

.stories_div{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 4rem;
}

.card{
  /* min-width: 20rem; */
  /* width: 40vw; */
  margin-left: 30rem;
  margin-right: 30rem;
  padding: 4rem;
  background-color: white;
  box-shadow: black;
  border-radius: 0.5rem;
  color: black;
  padding-bottom: 4rem;
  /* align-items: center;
  justify-content: center; */
}



.card-button{
  flex-direction: row;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.card-button a{
  color: #92b4ec;
  font-weight: 400;
  font-size: 1.6rem;
}

.card-button a:last-child{
  color: red;
}

.form{
  
 
  margin-left: 45rem;
 
  
}

.form input{
  box-shadow: 12px white;
  border-radius: 10px;
  font-size: 30px;
  justify-content: center;
  text-align: center;
  text-transform: capitalize;
  border-color: white;
}

.pagination_btn{
  margin-left:50rem ;
  box-shadow: inset;
}
.pagination_btn .prev1{
  color: red;
  height: 35px;
  width: 100px;
 cursor: pointer;
 
  
}

.pagination_btn .next1{
  color: red;
  height: 35px;
  width: 100px;
  cursor: pointer;
  
}



.pagination_btn .prev1:hover{
  background-color: black;
}

.pagination_btn .next1:hover{
  background-color: black;
}